import axios from "axios"
import request from "@/utils/request";

//post
export function postAction(url, parameter) {
    return axios({
        url: url,
        method: "post",
        data: parameter
    })
}

//post json
export function postJSON(url, data) {
    return axios({
        url: url,
        method: "post",
        data: data,
        headers: {
            "Content-Type": "application/json;charset=UTF-8",
            "token": window.sessionStorage.getItem("laisonToken")
        }
    })
}

export function getJSON(url, data) {
    return axios({
        url: url,
        method: "get",
        data: data,
        headers: {
            "token": window.sessionStorage.getItem("laisonToken")
        }
    })
}

//post method= {post | put}
export function httpAction(url, parameter, method) {
    return axios({
        url: url,
        method: method,
        data: parameter
    })
}

//put
export function putAction(url, parameter) {
    return axios({
        url: url,
        method: "put",
        data: parameter
    })
}

//get
export function getAction(url, parameter) {
    return axios({
        url: url,
        method: "get",
        params: parameter,
        headers: {
            "token": window.sessionStorage.getItem("laisonToken")
        }
    })
}

//post
export function postFormAction(url, parameter) {
    return axios({
        url: url,
        method: "post",
        params: parameter
    })
}

//deleteAction
export function deleteAction(url, parameter = "") {
    return axios({
        url: url,
        method: "delete",
        params: parameter
    })
}

export function downFile(url, parameter) {
    return axios({
        url: url,
        params: parameter,
        method: "get",
        responseType: "blob",
        headers: {
            "token": window.sessionStorage.getItem("laisonToken")
        }
    })
}

export function downFile2(url, fileName) {
    return axios({
        url: url,
        method: "get",
        responseType: "blob"
    })
}


