<template>
  <div>
    <a-layout>
      <a-layout-header
          :style="{padding: '0 0px', position: 'fixed', zIndex: 999, width: '100%',backgroundColor:'dodgerblue'}">
        <div class='header flex flex-space-between flex-align-center'>
          <div class='header-left'>
            <img @click='onSettingClick' class='header-logo' src='http://www.laisontech.com/lapis/icon/laison_logo.png'>
          </div>

          <div class='header-right flex flex-align-center'>
            <a-popover class='header-logo' placement="topLeft" arrow-point-at-center>
              <template slot="content">
                <h3 class='dd-cover' @click='onUserClick'></h3>
                <h3 class='dd-cover' @click='onChangePwdClick'></h3>
                <h3 class='dd-cover' @click='onQuitClick'></h3>
              </template>
              <img :src='userInfo.avatar'>
            </a-popover>
            <span class='header-user-name'>
                 ,{{ userInfo.name }}!
            </span>
          </div>

          <!--          <div class='header-right flex flex-align-center' @click='onUserClick'>-->
          <!--            <img class='header-logo' :src='userInfo.avatar'>-->
          <!--            <span class='header-user-name'>-->
          <!--            ,{{ userInfo.name }}!-->
          <!--          </span>-->
          <!--          </div>-->

        </div>
      </a-layout-header>
      <a-layout-content :style="{ padding: '0 0px', marginTop: '64px' }">
        <div :style="{ background: '#fff',  minHeight: '600px' }">
          <ContentComponent @processCodeSelect='onProcessCodeSelect'
                            @cloumnsFinish='onCloumnsFinish'
                            :processColumnsChange='columnsChange'></ContentComponent>
        </div>
      </a-layout-content>
      <!--    <a-layout-footer :style="{ textAlign: 'center' }">-->
      <!--      Ant Design 2018 Created by Ant UED-->
      <!--    </a-layout-footer>-->

    </a-layout>


    <!---->
    <a-modal title="" v-model='showUserForm' :mask-closable='false' :width="500"
             :footer='null'>
      <div v-if='showUserForm'>
        <component :is='UserForm' :user-info='userInfo' @loginout='signOut'></component>
      </div>
    </a-modal>
    <!---->
    <a-modal title="" v-model='showPwdForm' :mask-closable='false' :width="500"
             :footer='null'>
      <div v-if='showPwdForm'>
        <component :is='PwdForm' :user-info='userInfo' @changeSuccess='signOut'></component>
      </div>
    </a-modal>
    <!---->
    <a-modal title="" okText='' cancel-text=""
             v-model='showSettingForm'
             :mask-closable='false'
             :width="'60%'"
             @ok='operateColumn'>
      <div v-if='showSettingForm'>
        <component :is='SettingForm' :process-code='processCode'
                   @checkColumns='onCheckCol'
                   @unCheckColumns='onUnCheckCol'></component>
      </div>
    </a-modal>
  </div>
</template>

<script>
import ContentComponent from "@/components/ContentComponent";
import {getLoginUser, loginout, changePColumns} from "@/api/laisonorder";
import {OrderMixin} from "@/mixins/OrderMixin"
import {JeecgListMixin} from "@/mixins/JeecgListMixin";
import router from "@/routes/mainRoutes";

export default {
  name: "Home",
  components: {ContentComponent},
  mixins: [JeecgListMixin, OrderMixin],
  data() {
    return {
      showUserForm: false,
      showPwdForm: false,
      showSettingForm: false,
      userInfo: {},
      processCode: "",
      columnsChange: false,
      checkColumns: [],
      unCheckColumns: []
    }
  },
  mounted() {
  },
  created() {
    this.getUserInfo()
  },
  methods: {
    getUserInfo() {
      getLoginUser().then((res) => {
        this.userInfo = res.result
      }).catch((err) => {

      })
    },
    onUserClick() {
      this.showUserForm = true
    },
    onChangePwdClick() {
      this.showPwdForm = true
    },
    onQuitClick() {
      this.signOut()
    },
    signOut() {
      loginout().then((res) => {
        window.sessionStorage.setItem("laisonToken", "")
        router.push("/")
      }).catch((err) => {

      })
    },
    //
    onSettingClick() {
      this.showSettingForm = true
    },
    onProcessCodeSelect(processCode) {
      this.processCode = processCode
    },


    onCloumnsFinish() {
      this.columnsChange = false
    },
    onCheckCol(checkColumns) {
      this.checkColumns = checkColumns
    },
    onUnCheckCol(unCheckColumns) {
      this.unCheckColumns = unCheckColumns
    },
    operateColumn() {
      this.setType(this.checkColumns, 0)
      this.setType(this.unCheckColumns, 1)
      this.checkColumns.push(...this.unCheckColumns)
      if (!this.checkColumns || this.checkColumns.length < 1) {
        this.clear()
        return
      }
      changePColumns(this.processCode, this.checkColumns).then((res) => {
        this.columnsChange = !!res
        this.clear()
      }).catch((err) => {
        this.columnsChange = false
        this.clear()
      })
    },
    setType(columns, type) {
      if (columns && columns.length > 0) {
        for (let i = 0; i < columns.length; i++) {
          columns[i].type = type
          columns[i].sort = i
        }
      }
    },
    clear() {
      this.showSettingForm = false
      this.checkColumns = []
      this.unCheckColumns = []
    },
    confirmSelect(res) {
      console.log("click", res)
    }
  }
}
</script>

<style>

.flex {
  display: flex;
}

.flex-space-between {
  justify-content: space-between;
}

.flex-align-center {
  align-items: center;
}

.header {
  /*position:relative;*/
  /*width:100%;*/
  /*height:64px;*/
  /*line-height:64px;*/
  /*text-align:center;*/
}

.header-left, .header-right {
  /*position:absolute;*/
  /*height:64px;*/
  /*line-height:64px;*/
}

.header-left {
  /*left:15px;*/
  margin-left: 15px;
  cursor: pointer;
}

.header-right {
  /*right:20px;*/
  margin-right: 20px;
  cursor: pointer;
}

.header-logo {
  height: 40px;
  width: 40px;
  border-radius: 20px;
}

.header-user-name {
  color: white;
  margin-left: 8px;
  font-size: 16px;
}

#components-a-popconfirm-demo-placement .ant-btn {
  width: 70px;
  text-align: center;
  padding: 0;
  margin-right: 8px;
  margin-bottom: 8px;
}

.dd-cover {
  color: black;
  cursor: pointer;
}

.dd-cover:hover {
  color: dodgerblue;
}

.dd-cover:active {
  color: #666666;
}

</style>
