      <a-table
          :rowKey="(record)=> record.id+record.value"
          ref="table"
          size="small"
          bordered
          :scroll="{x:scrollX,y:'calc(100vh - 400px)'}"
          :columns="columns"
          :dataSource="dataSource"
          :pagination="pageInfo"
          :loading="loading"
          @change="handleTableChange">
        <template slot='DefaultField' slot-scope='text, record'>
          <div>{{ previewDefaultFieldValue(text) }}</div>
        </template>
        <template slot='TableFiled' slot-scope='text, record'>
          <div class='preview-file' @click='onTableFiledClick(record,text)'>
            {{ previewTableFiled(text) }}
          </div>
        </template>
        <template slot='DDAttachField' slot-scope='text, record'>
          <div class='dd-cover' @click='onDDAttachFieldClick(record,text)'>
            {{ previewDDAttachField(text) }}
          </div>
        </template>

        <template slot='OrderFile' slot-scope='text, record'>
          <div class='dd-cover' @click='onOrderFileClick(record,text)'>
            {{ previewOrderFileInfo(record, text) }}
          </div>
        </template>
        <span slot="action" slot-scope="text, record">
           <a href="javascript:;" @click="handleRecord(record)"></a>
                    <a-divider type="vertical"/>
            <a href="javascript:;" @click="handleDetail(record)"></a>
        </span>
      </a-table>
    </div>


    <!---->
    <a-modal title="" v-model='selectOrderVisible' :mask-closable='false' :width="1350"
             :footer='null'>
      <div v-if='selectOrderVisible'>
        <component :is='OrderDetailModal' :order-detail='selectOrderDetail'></component>
      </div>
    </a-modal>

    <!---->
    <a-modal title="" v-model='showTableForm' :mask-closable='false' :width="1200"
             :footer='null'>
      <div v-if='showTableForm'>
        <component :is='TableForm' :form-detail='formDetail'></component>
      </div>
    </a-modal>
    <!---->
    <a-modal title="" v-model='showOperateRecord' :mask-closable='false' :width="700"
             :footer='null'>
      <div v-if='showOperateRecord'>
        <component :is='OrderOperateRecord' :operate-detail='operateDetail'></component>
