<script>
import {JeecgListMixin} from "@/mixins/JeecgListMixin"
import {OrderMixin} from "@/mixins/OrderMixin"
import moment from "moment";
import {
  getPCodes,
  getPColumns,
  getPCondition,
  getPDetail,
  fRefreshData
  // getFileUrl
} from "@/api/laisonorder";
import router from "@/routes/mainRoutes";

export default {
  name: "Content",
  mixins: [JeecgListMixin, OrderMixin],
  props: {
    processColumnsChange: {
      type: Boolean,
      required: true
    }
  },
  watch: {
    processColumnsChange: function (newval, oldval) {
      if (newval) {
        //
        this.loadDefaultData()
        this.$emit("cloumnsFinish")
      }
    }
  },
  data() {
    return {
      scrollX: 4800,
      states: [],
      isSync: false,
      common: {},
      description: "",
      // 
      show: true,
      selectOrderDetail: {},
      selectOrderVisible: false,
      formDetail: [],
      showTableForm: false,
      operateDetail: {},
      showOperateRecord: false,
      selectDept: [],
      dataSource: [],
      processCode: "",
      defaultProcessCode: "PROC-5448AE23-2822-4197-913F-E39E6C7F8E78",
      processCodes: [
        {
          id: "PROC-5448AE23-2822-4197-913F-E39E6C7F8E78",
          name: ""
        }
      ],
      queryParam: {},
      customers: [],
      depts: [],
      statuss: [],
      paymodes: [],
      results: [],
      types: [],
      units: [],
      products: [],
      // 
      columns: []
    }
  },
  created() {
    //codes,
    this.getProcessCodes()
  },
  computed: {},
  methods: {
    moment,

    previewDefaultFieldValue(text) {
      let res = ""
      if (text) {
        let info = this.isJsonStr(text)
        if (info) {
          res = info.value
          if (!res) {
            res = info
          }
        } else {
          res = text
        }
      }
      res = res + ""
      res = this.replaceOtherStr(res)
      if (res.length > 22) {
        res = res.substring(0, 11) + "..." + res.substring(res.length - 11, res.length)
      }
      return res
    },

    previewTableFiled(text) {
      if (text && text != "null") {
        return this.buildValue(text)
      }
      return ""
    },
    previewDDAttachField(text) {
      if (text && text != "null") {
        return this.buildValue(text)
      }
      return ""
    },
    buildValue(record) {
      if (record.value && record.value !== "null") {
        return ""
      } else {
        return ""
      }
    },
    onTableFiledClick(record, text) {
      this.onTableBriefClick(text)
    },
    onDDAttachFieldClick(record, text) {
      this.onTableBriefClick(text)
    },
    //
    onOrderFileClick(record, text) {
      if (text && text.value && text.value !== "null") {
        let jsonValue = JSON.parse(text.value)
        if (jsonValue) {
          if (jsonValue.length === 1) {
            //
            this.downloadFile(record.id, jsonValue[0])
          } else {
            //

          }
        }
      }
    },
    /**
     * 
     * @param procInstId id
     * @param fileInfo 
     */
    downloadFile(procInstId, fileInfo) {
      this.loading = true
      this.getFileUrl(procInstId, fileInfo).then((res) => {
        this.loading = false
        this.downloadByBlob(res, fileInfo.fileName)
      }).catch((err) => {
        this.loading = false
      })
    },

    //
    previewOrderFileInfo(record, text) {
      let value = ""
      if (text && text.value && text.value !== "null") {
        let jsonValue = JSON.parse(text.value)
        if (jsonValue && jsonValue.length > 0) {
          for (let i = 0; i < jsonValue.length; i++) {
            let file = jsonValue[i]
            value += file.fileName + ","
          }
        }
      }
      if (value.length > 0) {
        value = value.substring(0, value.length - 1)
      }
      if (value.length > 28) {
        value = value.substring(0, 14) + "..." + value.substring(value.length - 14, value.length)
      }
      return value
    },

    onTableBriefClick(text) {
      this.showTableForm = true
      let record = text
      if (!record) {
        record = {}
      }
      this.formDetail = record
    }
    ,
    handleTableChange(page) {
      this.pageInfo = page
      this.loadData();
    }
    ,
    loadDefaultData() {
      //
      this.getColumns()
      //
      this.getScreenCondition()
      //
      this.loadData()
    }
    ,
    onProcessCodeSelect(value) {
      if (this.processCode != value) {
        this.processCode = value
        this.queryParam = {}
        this.dataSource = []
        this.pageInfo.current = 1
        this.pageInfo.total = 0
        this.onProcessSelect(this.processCode)
        this.loadDefaultData()
      }
    }
    ,

    loadData() {
      let param = {
        customer_name: this.queryParam.customer_name,
        originator_dept_id: this.queryParam.originator_dept_id,
        start_time: this.queryParam.startTime,
        end_time: this.queryParam.endTime,
        status: this.queryParam.status,
        contract_no: this.queryParam.contract_no,
        order_type: this.queryParam.order_type,
        pay_mode: this.queryParam.pay_mode,
        result: this.queryParam.result,
        order_unit: this.queryParam.order_unit,
        page: this.pageInfo.current,
        size: this.pageInfo.pageSize
      }
      this.loading = true
      getPDetail(this.processCode, param).then((res) => {
        this.isSync = false
        this.loading = false
        if (res) {
          let result = res.result
          if (result) {
            this.dataSource = result.list
            this.pageInfo.total = result.total
          }
          // console.log("dataSource",this.dataSource)
        } else {
          this.showErrorMsg("", null, true)
        }
      }).catch((err) => {
        this.loading = false
        this.showErrorMsg(err.errmsg, err.errcode, true)
      })
    }
    ,
    getProcessCodes() {
      this.loading = true
      getPCodes().then((res) => {
        this.loading = false
        if (res) {
          let proCodes = res.result
          if (proCodes != null && proCodes.length > 0) {
            this.processCodes = proCodes
            this.processCode = proCodes[0].code
            this.defaultProcessCode = this.processCode
            this.onProcessSelect(this.processCode)
            this.loadDefaultData()
          }
        } else {
          this.showErrorMsg("code", null, true)
        }
      }).catch((err) => {
        this.loading = false
        this.showErrorMsg(err.errmsg, err.errcode, true)
      })
    }
    ,
    getColumns() {
      getPColumns(this.processCode).then((res) => {
        if (res) {
          let serveColumns = res.result
          if (serveColumns != null && serveColumns.length > 0) {
            let columns = []
            for (let i = 0; i < serveColumns.length; i++) {
              let column = serveColumns[i]
              if (column.type !== 0) continue
              let onceColumn = {
                title: column.pname,
                dataIndex: column.pid,
                align: "center",
                width: this.buildColumnWidth(column.pname, column.pvalue)
              }
              let renders = this.checkAndSetRender(column)
              if (renders) {
                onceColumn.scopedSlots = renders[0]
                onceColumn.title = renders[1]
              }
              columns.push(onceColumn)
            }
            //
            columns.push({
              title: "",
              align: "center",
              width: "180",
              fixed: "right",
              dataIndex: "action",
              scopedSlots: {customRender: "action"}
            })
            this.columns = columns
          }
          // console.log("columns",this.columns)
          this.scrollX = this.calculateCloumnWidth(this.columns)
        } else {
          this.showErrorMsg("", null, true)
        }
      }).catch((err) => {
        this.showErrorMsg(err.errmsg, err.errcode, true)
      })
    }
    ,
    getScreenCondition() {
      getPCondition(this.processCode).then((res) => {
        if (res && res.result) {
          this.customers = res.result.customer
          this.depts = res.result.department
          this.statuss = res.result.status
          this.paymodes = this.buildPaymentModes(res.result.pay_mode)
          this.results = this.buildPaymentResults(res.result.result)
          this.types = res.result.order_type
          this.units = res.result.money_unit
        } else {
          this.showErrorMsg("", null, true)
        }
      }).catch((err) => {
        this.showErrorMsg(err.errmsg, err.errcode, true)
      })
    },
    buildPaymentModes(payModes) {
      if (!payModes || payModes.length < 1) return []
      for (let i = 0; i < payModes.length; i++) {
        if (!payModes[i].pay_mode) {
          payModes[i].pay_mode = "N/A"
        }
      }
      return payModes
    },
    buildPaymentResults(results) {
      if (!results || results.length < 1) return []
      for (let i = 0; i < results.length; i++) {
        if (!results[i].result) {
          results[i].result = "N/A"
        }
      }
      return results
    },

    onRangeTimeChange(dates, dateStrings) {
      this.queryParam.startTime = dateStrings[0]
      this.queryParam.endTime = dateStrings[1]
      if (this.queryParam.startTime != "") {
        this.queryParam.startTime += " 00:00:00"
      }
      if (this.queryParam.endTime != "") {
        this.queryParam.endTime += " 23:59:59"
      }
    }
    ,
    handleDetail(record) {
      this.selectOrderVisible = true
      this.selectOrderDetail = record
    }
    ,
    handleRecord(record) {
      this.showOperateRecord = true
      this.operateDetail = record
    }
    ,
    initDefault() {
      this.dataSource = []
      this.pageInfo.current = 1
      this.pageInfo.total = 0
    }
    ,
    searchData() {
      this.initDefault()
      this.loadData()
    }
    ,
//,ui
    searchReset() {
      this.queryParam = {}
      this.searchData()
    }
    ,
//
    forceNewData() {
      this.loading = true
      this.isSync = true
      fRefreshData().then((res) => {
        if (res) {
          this.searchReset()
        } else {
          this.showErrorMsg(res.errmsg, res.errcode, true)
        }
      }).catch((err) => {
        if (err.errcode === 1) {
          this.$message.error(err.errmsg)
        } else {
          this.showErrorMsg(res.errmsg, res.errcode, true)
        }
      })
    }
    ,
    checkAndSetRender(column) {
      let id = column.pid
      if (id) {
        let render = null
        if (id.indexOf("TableField_") !== -1) {
          //
          render = []
          render[0] = {customRender: "TableFiled"}
          render[1] = column.pname + id.substring(id.length - 5)
        } else if (id.indexOf("DDAttachment_") !== -1 &&
            id !== "DDAttachment_P9BUN4IEEJK0" &&
            id !== "DDAttachment_1UE8HW3ATUCG0" &&
            id !== "DDAttachment_1E1VEUVWDBNK0") {
          // 
          render = []
          render[0] = {customRender: "DDAttachField"}
          render[1] = column.pname
        } else if (id === "DDAttachment_1UE8HW3ATUCG0" || id === "DDAttachment_1E1VEUVWDBNK0") {
          //
          render = []
          render[0] = {customRender: "OrderFile"}
          render[1] = column.pname
        } else {
          //
          render = []
          render[0] = {customRender: "DefaultField"}
          render[1] = column.pname
        }
        return render
      }
      return null
    }
    ,
    handleDeptChange(val) {
      this.queryParam.dept = val
    }
    ,
    handleStateChange(val) {
      this.queryParam.state = val
    }
    ,
    handleProductChange(val) {
      this.queryParam.order_product = val
    }
    ,
    handleTypeChange(val) {
      this.queryParam.order_type = val
    }
    ,
    handleUnitChange(val) {
      this.queryParam.order_unit = val
    }
    ,
    showErrorMsg(msg, code, lastIndex) {
      if (lastIndex) {
        this.loading = false
        this.isSync = false
      }
      let errorMsg
      if (code && code != null) {
        errorMsg = "errorCode" + code + " , errorMsg" + msg
      } else {
        errorMsg = msg
      }
      this.$message.error(errorMsg)
    }
    ,
    calculateCloumnWidth(cloumns) {
      let width = 0
      for (let i = 0; i < cloumns.length; i++) {
        width += parseInt(cloumns[i].width)
      }
      return width
    }
    ,
    compare(prop, align) {
      return function (a, b) {
        var value1 = a[prop];
        var value2 = b[prop];
        if (align == "positive") {//
          return new Date(value1) - new Date(value2);
        } else if (align == "inverted") {//
          return new Date(value2) - new Date(value1);
        }
      }
    }
    ,
    onProcessSelect(processCode) {
      this.$emit("processCodeSelect", processCode)
    }

  }
}
</script>
