import axios from "axios";
import {Message} from "ant-design-vue"
import router from "@/routes/mainRoutes";

const service = axios.create({
    baseURL: process.env.VUE_APP_API_BASE_URL,
    timeout: 10000 // request timeout
})

service.interceptors.request.use(
    config => {
        let laisonToken = window.sessionStorage.getItem("laisonToken");
        if (laisonToken) {
            config.headers["token"] = laisonToken
        }
        return config
    },
    error => {
        //
        console.log("request:error", error)
        return Promise.reject(error)
    }
)

service.interceptors.response.use(
    response => {
        const status = response.status
        if (status !== 200) {
            return Promise.reject({errcode: status, errmsg: (response.statusText ? response.statusText : "")})
        }
        const res = response.data
        if (!res) {
            return Promise.reject({errcode: status, errmsg: (response.statusText ? response.statusText : "")})
        }
        if (res.errcode===undefined||res.errcode===null) {
            return response
        }
        if (res.errcode !== 0) {
            if (res.errcode === 50008) { //token 
                window.sessionStorage.setItem("laisonToken", "")
                router.push("/")
            }
            return Promise.reject({errcode: res.errcode, errmsg: (res.errmsg ? res.errmsg : "Error")})
        } else {
            if (response.headers) {
                if (response.headers.authorization) {
                    window.sessionStorage.setItem("laisonToken", response.headers.authorization)
                }
            }
            return res
        }
    },
    error => {
        // console.log("", JSON.stringify(error))
        if (error.message) {
            if (error.message.indexOf("50008") !== -1) {    //token
                window.sessionStorage.setItem("laisonToken", "")
                router.push("/")
                return Promise.reject({errcode: 50008, errmsg: "token"})
            } else {
                return Promise.reject({errcode: 500, errmsg: error.message})
            }
        } else {
            return Promise.reject({errcode: 500, errmsg: JSON.stringify(error)})
        }

    }
)

export default service
